.header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin: 0px;
    padding: 0px;
    background: whitesmoke;
    position: fixed;
    width: 100%;
    margin-top: -10px;
    z-index: 1;
}

.header-name {
    font-size: 40px;
    font-weight: 600;
    text-transform: capitalize;
    color: rgb(31, 31, 31);
    display: flex;
    flex-direction: column;
    text-align: center;

    span {
        text-transform: lowercase;
        font-weight: 300;
        font-size: 20px;
    }
}

.logo-wrapper {
    padding: 5px 10px;

    .logo{
        height: 50px;
    }
}

nav {
    width: 30%;
    padding: 30px 50px;
    background:rgb(31, 31, 31);
    border-bottom-left-radius: 80px;
    align-items: center;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;

    ul {
        display: flex;
        list-style: none;
        width: 100%;
        justify-content: space-between;

        li {
            margin: 0px 5px;

            a {
                text-decoration: none;
                color: white;
                transition: 0.2s;
            }
            a:hover {
                border-bottom: 3px solid white;
                font-weight: 600;
            }

        }
    }
}

@media screen and (max-width: 900px) {
    
    nav {
        display: none;
    }
}